<template>
  <header class="header" v-on:scroll="handleScroll" v-bind:class="{ onscroll: onScroll, active: headerActive}">
    <div class="container">
      <div class="header__inner">
        <a href="#" class="logo">
          <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M0.5 2.28577H6.28571C10.4278 2.28577 13.7857 5.64363 13.7857 9.78577V13.7858H8C3.85787 13.7858 0.5 10.4279 0.5 6.28577V2.28577Z" stroke="#DAA520"/>
            <path d="M16.2143 16.2142H22C26.1421 16.2142 29.5 19.5721 29.5 23.7142V27.7142H23.7143C19.5722 27.7142 16.2143 24.3564 16.2143 20.2142V16.2142Z" stroke="#DAA520"/>
            <path d="M16.2143 8C16.2143 3.85787 19.5722 0.5 23.7143 0.5H27.7143V6.28571C27.7143 10.4278 24.3564 13.7857 20.2143 13.7857H16.2143V8Z" stroke="#DAA520"/>
            <path d="M2.28571 23.7142C2.28571 19.5721 5.64357 16.2142 9.78571 16.2142H13.7857V21.9999C13.7857 26.1421 10.4278 29.4999 6.28571 29.4999H2.28571V23.7142Z" stroke="#DAA520"/>
          </svg>
          <img :src="require(`../assets/images/logo.png`)" alt="logo">
        </a>

        <nav class="menu">
          <ul class="menu__list">
            <li class="menu__list-item"><a href="#top" class="menu__list-link" @click="toggleHeaderMenu">Главная</a></li>
            <li class="menu__list-item"><a href="#about" class="menu__list-link" @click="toggleHeaderMenu">О нас</a></li>
            <li class="menu__list-item"><a href="#how" class="menu__list-link" @click="toggleHeaderMenu">На заказ</a></li>
            <li class="menu__list-item"><a href="#categories" class="menu__list-link" @click="toggleHeaderMenu">Категории</a></li>
            <li class="menu__list-item"><a href="#reviews" class="menu__list-link" @click="toggleHeaderMenu">Отзывы</a></li>
          </ul>
        </nav>

        <button class="transparent__btn" @click="$parent.toggleModal('signupForm')">Регистрация</button>

        <button class="header__hamburger" @click="toggleHeaderMenu">
          <span class="header__hamburger-box">
            <span class="header__hamburger-inner"></span>
          </span>
        </button>

      </div>
    </div>
  </header>
</template>

<script>


export default {
  data() {
    return {
      onScroll: false,
      headerActive: false
    }
  },

  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.onScroll = window.scrollY > 80;
    },

    toggleHeaderMenu() {
      let body = document.querySelector('body');
      if (window.innerWidth <=991) {
        this.headerActive = !this.headerActive;

        if (this.headerActive) {
          body.style.overflow = "hidden";
        } else body.style.overflow = "auto";
      }
    }
  }
}
</script>
