<script>

export default {
  data() {
    return {
      email: '',
      activeBtn: false
    }
  },

  methods: {
    validateEmail() {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      this.activeBtn = !!re.test(String(this.email).toLowerCase());
    },

    subscribe: function(event) {
      event.preventDefault();
      this.$parent.toggleModal('success');
      this.email = '';
      this.activeBtn = false;
    }
  }
}

</script>
