<template>
  <form class="signup__form">
    <h2 class="signup__form-title">Регистрация</h2>
    <input type="email" class="signup__form-input" v-model="email" placeholder="Email" @input="validate">
    <input type="password" class="signup__form-input" v-model="password" placeholder="Password" @input="validate">
    <button :disabled="!activeBtn" class="orange__btn" @click="signUp">Отправить</button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      email: '',
      password: '',
      activeBtn: false
    }
  },

  methods: {
    signUp: function(event) {
      event.preventDefault();
      this.$parent.$parent.toggleModal('success');
    },

    validate() {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      this.activeBtn = re.test(String(this.email).toLowerCase()) && this.password.length > 4;
    },
  }
}
</script>
